import { InferenceSession, Tensor } from "onnxjs";
import _ from "lodash";
import React, {
  FunctionComponent,
  useEffect,
  useState,
  useRef
} from "react";
import ndarray from "ndarray";
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import ops from "ndarray-ops";
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import { ImageCapture } from "image-capture";
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import {
  setIntervalAsync,
  clearIntervalAsync,
  SetIntervalAsyncTimer
} from "set-interval-async/dynamic";
import ResNet from "./resnet50_8.onnx";
import imageNetClasses from "./imageNetClasses";
import { StyledCameraFeed, StyledVideoFrame } from "../Camera/Camera.styled";
import getMediaStream from "../browser/getMediaStream";

const ImageNet: FunctionComponent = () => {
  const [modelLoaded, setModelLoaded] = useState(false);
  const video = useRef<HTMLVideoElement>(null);
  const frame = useRef<HTMLCanvasElement>(null);
  const predictions = useRef<HTMLDivElement>(null);
  const resNetDims = 224;

  useEffect(() => {
    let interval: SetIntervalAsyncTimer;
    let mediaStream: MediaStream;
    let track: MediaStreamTrack;

    const videoConstraints = {
      audio: false,
      video: {
        width: {
          min: resNetDims,
          ideal: resNetDims,
          max: resNetDims
        },
        height: {
          min: resNetDims,
          ideal: resNetDims,
          max: resNetDims
        }
      }
    };

    const getImageData = () => {
      const width = _.get(frame, "current.width") as number;
      const height = _.get(frame, "current.height") as number;
      const ctx = frame.current?.getContext("2d");
      if (ctx && width && height) {
        return ctx.getImageData(0, 0, resNetDims, resNetDims) as ImageData;
      }
      return null;
    };

    const preprocess = (imageData: ImageData | null) => {
      if (!imageData) {
        return null;
      }
      const dataFromImage = ndarray(new Float32Array(imageData.data), [imageData.width, imageData.height, 4]);
      const dataProcessed = ndarray(new Float32Array(imageData.width * imageData.height * 3), [1, 3, imageData.height, imageData.width]);

      // Normalize 0-255 to (-1)-1
      ops.divseq(dataFromImage, 128.0);
      ops.subseq(dataFromImage, 1.0);

      // Realign imageData from [resNetDims*resNetDims*4] to the correct dimension [1*3*resNetDims*resNetDims].
      ops.assign(dataProcessed.pick(0, 0, null, null), dataFromImage.pick(null, null, 2));
      ops.assign(dataProcessed.pick(0, 1, null, null), dataFromImage.pick(null, null, 1));
      ops.assign(dataProcessed.pick(0, 2, null, null), dataFromImage.pick(null, null, 0));

      return dataProcessed.data;
    };

    const processFrame = (imageBitmap: CanvasImageSource) => {
      if (frame.current) {
        frame.current.width = imageBitmap.width as number;
        frame.current.height = imageBitmap.height as number;
        const frame2d = frame.current.getContext("2d");
        if (frame2d) {
          frame2d.drawImage(imageBitmap, 0, 0);
        }
      }
    };

    const imagenetClassesTopK = (classProbabilities: Float32Array, k = 5) => {
      const probs = Array.from(classProbabilities);
      const probsIndices = probs.map(
        (prob, index) => [prob, index]
      );
      const sorted = probsIndices.sort(
        (a, b) => {
          if (a[0] < b[0]) {
            return -1;
          }
          if (a[0] > b[0]) {
            return 1;
          }
          return 0;
        }
      ).reverse();
      const topK = sorted.slice(0, k).map((probIndex) => {
        const iClass = imageNetClasses[probIndex[1]];
        return {
          id: iClass[0],
          index: probIndex[1],
          name: iClass[1].replace(/_/g, " "),
          probability: probIndex[0]
        };
      });
      return topK;
    };

    const printMatches = (data: Float32Array) => {
      let outputClasses = [];
      if (!data || data.length === 0) {
        const empty = [];
        for (let i = 0; i < 5; i += 1) {
          empty.push({ name: "-", probability: 0, index: 0 });
        }
        outputClasses = empty;
      } else {
        outputClasses = imagenetClassesTopK(data, 5);
      }
      if (predictions.current) {
        predictions.current.innerHTML = "";
        const results = [];
        for (let i = 0; i < 5; i += 1) {
          results.push(`${outputClasses[i].name}: ${Math.round(100 * outputClasses[i].probability)}%`);
        }
        predictions.current.innerHTML = results.join("<br/>");
      }
    };

    const stopCamera = (error: Error | null = null) => {
      console.error(error);
      if (track) {
        track.stop();
      }
    };

    const session = new InferenceSession(); // { backendHint: "wasm" }

    session
      .loadModel(ResNet)
      .then(() => setModelLoaded(true))
      .then(() => {
        getMediaStream(video.current, videoConstraints)
          .then((stream) => {
            if (!stream) {
              return;
            }
            mediaStream = stream;
            interval = setIntervalAsync(() => {
              // eslint-disable-next-line prefer-destructuring
              const tracks = mediaStream.getVideoTracks();
              track = tracks[tracks.length - 1];
              const imageCapture = new ImageCapture(track);
              if (imageCapture.track.readyState === "live"
            && imageCapture.track.enabled
            && !imageCapture.track.muted) {
                return imageCapture
                  .grabFrame()
                  .then((imgFrame: CanvasImageSource) => {
                    processFrame(imgFrame);
                    const preprocessedData = preprocess(getImageData()) as Float32Array;
                    const inputTensor = new Tensor(preprocessedData, "float32", [1, 3, resNetDims, resNetDims]);
                    return session.run([inputTensor]);
                  })
                  .then((outputMap: InferenceSession.TensorsMapType) => {
                    const outputData = outputMap.values().next().value.data as Float32Array;
                    printMatches(outputData);
                  })
                  .catch(stopCamera);
              }
              stopCamera();
              return getMediaStream(video.current, videoConstraints)
                .then((newStream) => {
                  if (!newStream) {
                    return;
                  }
                  mediaStream = newStream;
                });
            }, 17);
          });
      });

    return () => {
      clearIntervalAsync(interval);
      if (mediaStream) {
        mediaStream.getTracks().forEach((t) => {
          t.stop();
        });
      }
    };
  }, []);

  return (
    <div className="page">
      <h3>ResNet50 ImageNet Example</h3>
      {/* <StyledCameraFeed data-testid="camera-canvas-origin" id="video" ref={video} /> */}
      <StyledVideoFrame data-testid="camera-canvas-grab" id="frame" ref={frame} />
      <div id="predictions" ref={predictions} />
    </div>
  );
};

export default ImageNet;
