import _ from "lodash";
import React, { FunctionComponent, useEffect, useRef } from "react";
import { StyledCameraFeed } from "./Camera.styled";
import getMediaStream from "../browser/getMediaStream";

const Camera: FunctionComponent = () => {
  const video = useRef<HTMLVideoElement>(null);

  useEffect(() => {
    let mediaStream: MediaStream;

    const videoConstraints = {
      video: {
        width: {
          min: 1280,
          ideal: 1280,
          max: 1280
        },
        height: {
          min: 720,
          ideal: 720,
          max: 720
        }
      }
    };

    getMediaStream(video.current, videoConstraints)
      .then((stream) => {
        if (stream) {
          mediaStream = stream;
        }
      });

    return () => {
      if (mediaStream) {
        mediaStream.getTracks().forEach((t) => {
          t.stop();
        });
      }
    };
  }, []);

  return (
    <div className="page">
      <h3>Camera Example</h3>
      <StyledCameraFeed data-testid="camera-canvas-origin" id="video" ref={video} />
    </div>
  );
};

export default Camera;
