import React, {
  FunctionComponent, useEffect, useState, useRef
} from "react";
import AsyncMQTT from "async-mqtt";

const Mqtt: FunctionComponent = () => {
  const mqttClient = useRef<AsyncMQTT.AsyncMqttClient | null>(null);
  const [connected, setConnected] = useState<boolean | null>(null);

  useEffect(() => {
    AsyncMQTT
      .connectAsync("ws://broker.mqttdashboard.com:8000/mqtt", undefined, false)
      .then((client) => {
        mqttClient.current = client;
        setConnected(true);
        return mqttClient.current.subscribe("$SYS/broker/uptime");
      })
      .then((grant: AsyncMQTT.ISubscriptionGrant[]) => {
        console.log(grant);
        // mqttClient.on("message", (topic, message) => {
        //   console.log(message.toString());
        // });
      })
      .catch(() => setConnected(false));
    return () => {
      if (mqttClient.current) {
        mqttClient.current.end(true);
      }
    };
  }, []);

  return (
    <div className="page">
      <span>
        Broker: ws://broker.mqttdashboard.com:8000/mqtt
      </span>
      <br />
      {connected === null && <span>Connecting...</span>}
      {connected === false && <span>Failed to connect</span>}
      {connected && <span>CONNECTED</span>}
    </div>
  );
};

export default Mqtt;
